.content{
  width: 100%;
  min-width: 320px;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.content .main, .content .info{
  width: 100%;
  height: 60vh;
  min-width: 320px;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

@media (min-width: 800px) {
    .content{
      height: calc(100vh - 100px);
    }
    .content .main{
      width: 60%;
      min-width: 480px;
      height: 100%;
    }

    .content .info{
      width: 40%;
      min-width: 320px;
      height: 100%;
    }
}



